.page {
  scroll-behavior: smooth;

  &.lock {
    overflow: hidden;
    padding-right: 17px;
  }

  &__scroll {
    position: fixed;
    display: none;
    right: 20px;
    bottom: 30px;
    background-image: url("../images/scroll.png");
    height: 30px;
    width: 30px;

    @include tablet {
      right: 40px;
    }

    @include desktop {
      right: 55px;
    }
  }
}
