.nav {
  &__logo {
    @include zIndex;
    width: 117px;
    height: 26px;
    background-image: url("../images/logo.svg");
  }

  &__item {
    display: flex;
  }

  &__languages {
    margin-left: 40px;
    @extend %raleway--500-16-150;
    @include zIndex;
  }

  &__lang-choise {
    position: relative;
    padding-right: 10px;
    cursor: pointer;

    &::after {
      @include transition(all);
      content: url("../images/arrow.svg");
      display: block;
      position: absolute;
      right: -3px;
      top: -3px;
    }

    &.active::after {
      top: -50%;
      transform: rotate(180deg) translateY(-50%);
    }
  }

  &__lang-container {
    position: absolute;
    opacity: 0;
    visibility: hidden;
    transform: translateY(-50%);
    @include transition(all);

    &.active {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
    }
  }

  &__lang-items {
    color: $lightColor;
    text-decoration: none;
  }
}
