.visit {
  background-color: $headerColor;
  @include desktop {
    height: 100vh;
    background-image: url("../images/visit/img_07.png");
    background-repeat: no-repeat;
    background-position: 0 100%;
  }

  &__item {
    padding: 50px 0;
    @include tablet {
      padding: 80px 0;
    }
    @include desktop {
      display: grid;
      align-content: center;
      padding: 0;
    }
  }
}
