.button {
  width: 270px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  @include transition(all);
  text-decoration: none;
  cursor: pointer;
  @extend %raleway--600-16-100;

  &--primary {
    border: 1px solid $orange;
    background-color: $orange;
    color: $lightColor;

    &:hover {
      background-color: #af3419;
    }

    &:active {
      background-color: #ea340d;
    }

    &:disabled {
      background-color: #dd836f;
      cursor: not-allowed;
    }
  }

  &--secondary {
    border: 1px solid $orange;
    color: $orange;
    background-color: #fff;

    &:hover {
      border-color: #af3419;
      color: #af3419;
    }

    &:active {
      border-color: #ea340d;
      color: #ea340d;
    }

    &:disabled {
      border-color: #dd836f;
      color: #dd836f;
      cursor: not-allowed;
    }
  }
}
