.form-section {

  &__email-input {
    border-color: transparent;
    border-bottom: 1px solid $darkColor;
    width: 100%;
    color: $darkColor;
    font-size: 16px;
    line-height: 150%;
    margin: 50px 0 35px;

    &::placeholder {
      @extend %raleway--400-16-150;
      color: $darkColor;
    }

    &:hover {
      border-bottom: 1px solid $orange;
      &::placeholder {
        color: $orange;
      }
    }

    &:focus {
      outline: none;
      border-bottom: 1px solid $orange;
      &::placeholder {
        color: $orange;
      }
    }

    &.filled {
      border-bottom: 1px solid $darkColor;
      @extend %raleway--700-16-150;
    }
  }

  &__text {
    margin: 20px 0 0;
  }
}
