.burger {
  &__icon {
    width: 25px;
    height: 20px;
    position: relative;
    z-index: 200;
    cursor: pointer;

    span {
      width: 100%;
      height: 2px;
      background-color: $lightColor;
      @include transition(all);
      position: absolute;
      left: 0;
    }

    & span:nth-child(1) {
      top: 0;
    }

    & span:nth-child(2) {
      top: 50%;
      transform: translateY(-50%);
    }

    & span:nth-child(3) {
      bottom: 0;
    }

    &.active span:nth-child(1) {
      top: 50%;
      transform: rotate(45deg) translate(0, -50%);
    }

    &.active span:nth-child(2) {
      transform: scale(0);
    }

    &.active span:nth-child(3) {
      bottom: 50%;
      transform: rotate(-45deg) translate(0, 50%);
    }
  }

  &__menu {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: $headerColor;
    transform: translateX(200%);
    @include transition(transform);
    overflow: auto;
    z-index: 100;
  }

  &__menu.active {
    transform: translateX(0);
    overflow: auto;
  }

  &__image {
    display: none;
    @include beforeDesktop {
      justify-content: center;
      overflow: hidden;
      height: 40%;
    }

    @media(min-width: 767px) and (max-width: 991px) {
      display: flex;
    }

    @include desktop {
      display: block;
      background-image: url("../images/header/img_02.png");
      position: absolute;
      right: 0;
      bottom: 0;
      @include optimizeImage;
    }
  }

  &__img {
    display: none;
    @include tablet {
      display: block;
      max-width: 100%;
      height: auto;
    }
    @include desktop {
      display: none;
    }
  }

  &__content {
    margin-top: 30px;
    @include beforeDesktop {
      flex: 2 1 auto;
      padding-bottom: 50px;
    }
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0 0 100px;
    position: relative;
    z-index: 3;
  }

  &__schedule {
    display: flex;
    justify-content: space-between;
  }

  &__columnTop {
    @extend %raleway--400-14-140;
    color: $lightColor;
  }

  &__columnBottom,
  &__adress {
    @extend %raleway--600-16-150;
    color: $lightColor;
  }

  &__adress {
    text-decoration: none;
  }

  &__link {
    text-decoration: none;
    color: inherit;
    position: relative;

    &:after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: $lightColor;
      opacity: 0;
      @include transition(opacity);
    }

    &:hover {
      &::after {
        opacity: 0.3;
      }
    }
  }

  &__line {
    width: 100%;
    height: 1px;
    background-color: $lightColor;
    opacity: 0.3;
  }
}
