.container {
  max-width: 1280px;
  padding: 0 20px;
  margin: 0 auto;

  @include tablet {
    padding: 0 40px;
  }

  @include desktop {
    padding: 0 55px;
  }

  &--header,
  &--burger {
    height: 100vh;
  }

  &--header {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    @include beforeDesktop {
      justify-content: space-between;
    }
  }

  &--burger {
    @include beforeDesktop {
      display: flex;
      height: 100%;
      flex-direction: column;
    }
    @include desktop {
      align-items: flex-end;
    }
  }

  &--visit {
    @include desktop {
      height: 100vh;
    }
  }
}
