.title {
  &__h1,
  &__h2,
  &__h3,
  &__h4 {
    @extend %Playfair;
  }

  &__h1 {
    font-size: 48px;
    color: $lightColor;

    @include tablet {
      font-size: 72px;
    }
    @include desktop {
      font-size: 96px;
    }
  }

  &__h2 {
    font-size: 28px;
    line-height: 120%;
    color: $darkColor;
    @include tablet {
      font-size: 40px;
    }
    @include desktop {
      font-size: 48px;
    }

    &--mod-colorSize {
      font-size: 38px;
      @include tablet {
        font-size: 62px;
      }
      @include desktop {
        font-size: 80px;
      }
      line-height: 100%;
      color: $lightColor;
    }

    &--mod-burgerColor {
      color: $lightColor;
    }
  }

  &__h3 {
    font-size: 24px;
    line-height: 125%;
    color: $darkColor;

    &--mod-color {
      color: $lightColor;
    }

    &--mod-margin {
      margin-bottom: 20px;
    }
  }

  &__h4 {
    font-size: 20px;
    line-height: 150%;
    color: $darkColor;

    &--mod-color {
      color: $lightColor;
    }
  }
}
