.section {
  margin-bottom: 110px;

  @include tablet {
    margin-bottom: 164px;
  }

  @include desktop {
    margin-bottom: 200px;
  }

  &--mod-mb {
    margin-bottom: 90px;

    @include tablet {
      margin-bottom: 100px;
    }

    @include desktop {
      margin-bottom: 110px;
    }
  }

  &__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 70px;
  }

  &__block {
    @include beforeTablet {
      margin-bottom: 60px;
    }

    &:nth-last-child(-n+2) {
      @include beforeTablet {
        margin-bottom: 30px;
      }
    }
  }

  &__link-image {
    margin-bottom: 20px;
    display: inline-block;
    overflow: hidden;
  }

  &__image {
    max-width: 100%;
    width: 100%;
    @include transition(transform);

    &:hover {
      @include desktop {
        transform: scale(1.1);
      }
    }
  }

  &__button-top {
    display: none;
    @include tablet {
      display: flex;
    }
  }

  &__button-top,
  &__button-bottom {
    & span {
      position: relative;
    }

    & span::after {
      content: url("../images/arrow-1.svg");
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: -25px;
    }
  }

  &__button-bottom {
    display: flex;
    @include tablet {
      display: none;
    }
  }

  &__time {
    display: flex;
    margin: 20px 0 10px;
    @extend %raleway--400-16-150;
    color: $orange;
    &--mod-color {
      color: $darkColor;
    }
  }

  &__text {
    @extend %raleway--400-16-150;
    color: $grey;

    &--mod-margin {
      margin: 20px 0 30px;

      @include tablet {
        margin: 20px 0 40px;
      }

      @include desktop {
        margin: 20px 0 50px;
      }
    }

    &--mod-maxWidth {
      max-width: 570px;
    }

    &--mod-color {
      color: $lightColor;
    }

    &--mod-darkColor {
      color: $darkColor;
    }
  }

  &__button,
  &__button-bottom {
    @media(max-width: 500px) {
      width: 100%;
    }
  }

}
