.header {
  background-color: $headerColor;
  height: 100vh;
  position: relative;

  &__body {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: 570px;
    position: relative;
    z-index: 3;
    @include desktop {
      flex: 2 1 auto;
      padding-bottom: $headerBodyPB;
    }
  }

  &__nav {
    display: flex;
    justify-content: space-between;
    padding: 30px 0;
  }

  &__date {
    @extend %raleway--400-14-140;
    color: $lightColor;
    position: relative;

    &--mod {
      display: none;
      @include desktop {
        display: block;
      }
      opacity: 0.7;

      &::after {
        content: "";
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: calc(100% - 101px);
        height: 1px;
        background-color: $lightColor;
        margin-left: 20px;
        opacity: 0.3;
      }
    }
  }

  &__title {
    margin: 10px 0 30px;
  }

  &__description {
    @extend %raleway--400-16-150;
    color: $lightColor;
    margin-bottom: 40px;
    @include desktop {
      margin-bottom: 80px;
    }
  }

  &__decoration-wrapper {
    overflow: hidden;
    @include beforeDesktop {
      display: flex;
      width: 100%;
      overflow: hidden;
      justify-content: center;
    }

    @include desktop {
      position: absolute;
      right: 0;
      bottom: 0;
      background: url("../images/header/img_01.png");
      @include optimizeImage;
    }
  }

  &__image {
    max-width: 100%;
    min-height: 297px;
    @include desktop {
      display: none;
    }
  }
}
