.footer {
  background-color: $headerColor;
  padding: 35px 0 30px;
  @include tablet {
    padding-top: 50px;
  }

  &__text {
    margin: 10px 0 30px;
    @include tablet {
      margin: 30px 0 50px;
    }

    &.footer__menu {
      margin-bottom: 0;
    }
  }

  &__text-link {
    text-decoration: none;
    color: $lightColor;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: #9ad9c9;
      opacity: 0;
      @include transition(opacity);
    }
    @include transition(color);

    &:hover {
      color: #9ad9c9;
      &::after {
        opacity: 1;
      }
    }
  }

  &__social {
    display: flex;
    align-items: center;
    width: 140px;
    justify-content: space-between;

    & path {
      @include transition(fill);
    }
    & path:hover {
      fill: #9ad9c9;
    }
  }

  &__menu-li {
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  &__bottom {
    display: flex;
    max-width: 350px;
    justify-content: space-between;
  }
}
